@media (min-width: 1025px) {
   .horizontalMenu>.horizontalMenu-list>li:last-child>ul.sub-menu {
	left: 0;
   }
    .horizontalMenu > .horizontalMenu-list > li > ul.sub-menu > li > ul.sub-menu{
	left:100%;
	right:auto;
   }
  .page {
    max-width: 1170px;
    margin: 0 auto;
    background: $background;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 5px 0px 0px, rgba(0, 0, 0, 0.3) 0px 5px 25px 0px;
  }

  body {
    background: #cacfda !important;
  }

  .app-header {
    max-width: 1170px;
    margin: 0 auto;
  }

  .sticky-pin .horizontal-main {
    max-width: 1170px;
  }

  .navbar {
    max-width: 1170px;
    margin: 0 auto;
  }

  .vh {
    height: 100vh;
  }

  .app-sidebar {
    right: auto !important;
  }

  .side-header {
    left: auto !important;
    right: auto !important;
    z-index: 1044;
    background: $white;
  }

  .second-sidemenu {
    right: auto !important;
    margin-right: 70px;
  }

  .app.sidebar-mini.sidenav-toggled4 .first-sidemenu {
    right: auto !important;
  }

  .error-image {
    background: none;

    .page.page-h {
      background-image: url(../images/pngs/error.png) !important;
      background-position: center !important;
      background-size: cover !important;
      background-attachment: fixed !important;
    }
  }
}